* {
    box-sizing: border-box;
}

body, html {
    height: 100%;
    margin: 0;
    border: 0;
    padding: 0;
    color: #f2f2f2;
    background-color: #888888;
}

.container {
    width: 98%;
    max-width: 1280px;
    margin: 0 auto;
}

h1, h2, h3, h4, h5, h6 {
    display: block;
    margin: 0;
    border: 0;
    padding: 0;
    font-weight: bold;
    line-height: 100%;
}

p {
    margin: 0 0 12px;
    border: 0;
    padding: 0;
    line-height: 160%;
}

.page-title {
    margin: 0 0 32px;
    border-bottom: 1px solid #aaaaaa;
    padding: 16px 1% 8px;
    font-size: 48px;
    line-height: 100%;
    text-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
}

.movie-wrap {
    font-size: 0;
}

.movie-wrap>* {
    display: inline-block;
    vertical-align: top;
    font-size: 16px;
}

.movie-wrap .loading {
    padding: 0 2%;
    font-size: 18px;
    font-weight: bold;
    text-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
}