.movie-card {
    width: 48%;
    height: 360px;
    margin: 28px 1%;
    border: 0;
    border-radius: 8px;
    padding: 12px 8px;
    background-color: rgba(0, 0, 0, 0.25);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}

.movie-card:after {
    content: '';
    display: table;
    clear: both;
}

.movie-card .movie-thumb {
    width: 200px;
    height: 300px;
    margin: -40px 12px 0 0;
    border-radius: 4px;
    padding: 4px;
    float: left;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
}

.movie-card .movie-thumb .movie-lang {
    display: inline-block;
    border-radius: 4px;
    padding: 2px 8px;
    background-color: rgba(0, 0, 0, 0.5);
}

.movie-card .movie-data {
    width: calc(100% - 212px);
    height: 100%;
    float: left;
    overflow: hidden;
}

.movie-card .movie-data .movie-title {
    margin: 0 0 8px;
    border-bottom: 1px solid #dddddd;
    padding-bottom: 8px;
    font-size: 24px;
    text-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
}

.movie-card .movie-data .movie-simple {
    margin: 0 0 16px;
    font-size: 14px;
}

.movie-card .movie-data .movie-simple:after {
    content: '';
    display: table;
    clear: both;
}

.movie-card .movie-data .movie-simple p {
    line-height: 100%;
    margin: 0;
}

.movie-card .movie-data .movie-simple .movie-runtime {
    float: left;
}

.movie-card .movie-data .movie-simple .movie-rating {
    float: right;
}

.movie-card .movie-data .movie-genres {
    list-style: none;
    margin: 0 0 12px;
    border: 0;
    padding: 0;
    font-size: 0;
}

.movie-card .movie-data .movie-genres li {
    display: inline-block;
    margin: 0 2px 4px;
    border: 1px solid rgba(0, 0, 0, 0.15);
    border-radius: 4px;
    padding: 2px 6px;
    font-size: 12px;
    background-color: rgba(0, 0, 0, 0.25);
}

.movie-card .movie-data .movie-summary {
    font-size: 14px;
    color: rgba(255, 255, 255, 0.5);
}